import React from "react";
import styled from "styled-components";
import fetch from "isomorphic-unfetch";
import { FaTag } from "react-icons/fa";

import Container from "../components/Container";
import ReadingContainer from "../components/ReadingContainer";
import ListItem from "../components/ListItem";

const Title = styled.h1`
  align-items: center;
  display: flex;
  font-size: 34px;
  line-height: 1.15;

  @media screen and (max-width: 768px) {
    font-size: 30px;
  }
`;

const Subtitle = styled.div`
  color: rgba(0, 0, 0, 0.54);
  font-size: 23px;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 30px;

  @media screen and (max-width: 1199px) {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 10px;
  }
`;

const Divider = styled.div`
  display: block;
  font-size: 35px;
  letter-spacing: 0.6em;
  text-align: center;
`;

const PostContainer = styled.li`
  margin-bottom: 10px;
`;

const AllPosts = ({ posts = [] }) => {
  const title = `${posts.length} Bible Questions and Answers | Bible Answers`;

  return (
    <Container>
      <title>{title}</title>
      <meta
        type="description"
        content={`All ${
          posts.length
        } of Bible Answers's questions and answers. Learn about and defend the Bible with these questions and answers about a wide range of categories.`}
      />

      <ReadingContainer style={{ marginBottom: 0 }}>
        <Title>{posts.length} Bible Questions and Answers</Title>
        <Subtitle>Browse all of our questions and answers</Subtitle>
        <Divider>...</Divider>
      </ReadingContainer>
      <ReadingContainer>
        <div className="writing">
          <h3>All posts</h3>
          {posts.map(post => {
            return (
              <a
                key={post.url}
                href={post.url}
                style={{ textDecoration: "none" }}
              >
                <ListItem {...post} />
              </a>
            );
          })}
        </div>
      </ReadingContainer>
    </Container>
  );
};

AllPosts.getInitialProps = async function({ query }) {
  const res = await fetch(`https://bibleanswersapi.herokuapp.com/all_posts`);
  const data = await res.json();

  return { posts: data };
};

export default AllPosts;
